

.black-nav {
  display: flex;
  background-color: #333;
  width: 100%;
  color: red;
  font-size: 20px;
  padding-left: 20px;
}

div {
  box-sizing: border-box;
}

.list {
  text-align-last: left;
  padding-left: 20px;
  border-bottom: 1px solid gray;
}
.modal {

  position: absolute;

  width: 80%;

  height: 50%;

  top: 50%;

  left: 50%;

  transform: translate(-50%, -50%);

  padding: 30px;

  background-color: #eee;

  text-align: left;

}